.etape{
    width: 100%;
    margin: 48px 0;
    &_link{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        
        a{
            display: flex;
            align-items: center;
            padding:16px 24px;
            border-radius: 30px;
            background: #E5E5EF;
            margin-right: 16px;
            &:last-child{
                margin-right: 0;
            }
            img{
                height: 12px;
                margin-right: 12px;
            }
        }
        .active{
            background: #B43FA4;
            img,span{
                filter: invert(1);
            }
        }
    }
}