.page_captation{
    padding-bottom:48px;
    height:calc(100vh - 219px);
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    &_flex{
        display: flex;
        align-items: center;
        justify-content: center;
        height:100%;
        overflow-y: scroll;
    }
    &_left{
        height:100%;
        margin-right: 24px;
        &_video{
            height:100%;
        }
    }
    &_right{
        &_son{
            margin-bottom: 24px;
        }
        &_commentaire{
            &_container{
                margin-bottom: 24px;
                padding-bottom: 24px;
                border-bottom: 1px solid #E5E5EF;
                &:last-child{
                    margin-bottom: 0;
                    border-bottom: none;
                }
                &_title{
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 16px;
                    h4{
                        font-weight: 600;
                    }
                }
            }
        }
        &_son,&_commentaire{
            height:calc(50% - 12px);
        }
    }
    &_left,&_right{
        max-width:475px;
        width: 100%;
        height:100%;
        overflow-y: scroll;
    }
    &_left_video,&_right_son,&_right_commentaire{
        padding:32px;
        border: 1px solid #E5E5EF;
        border-radius: 16px;
        overflow-y: scroll;
        &_title{
            display: flex;
            justify-content: space-between;
            align-items: baseline;
            margin-bottom: 32px;
            h4{
                font-size: 20px;
                font-weight: 700;
            }
            div{
                display: flex;
                button{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: #E5E5EF;
                    padding:12px;
                    &:nth-child(2){
                        margin-left: 12px;
                    }
                    img{
                        width: 12px;
                        height: 12px;
                        object-fit: cover;
                    }
                }
            }
        }
    }
    &_left_video,&_right_son{
        &_container{
            &_content{
                display: flex;
                justify-content: space-between;
                margin-bottom: 16px;
                &:last-child{
                    margin-bottom: 0;
                }
                span{
                    display: flex;
                    align-items: center;
                    img{
                        margin-right: 8px;
                    }
                }
            }
        }
    }
    &_valide{
        display: flex;
        justify-content: center;
        margin-top: 48px;
        button{
            background: #22C55E;
            display: flex;
            align-items: center;
            justify-content: center;
            img{
                margin-right: 8px;
                filter: invert(1);
            }
        }
    }
}