.page_accueil{
    padding:48px;
    height:calc(100vh - 75px);
    overflow: hidden;
    &_header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        &_title_add{
            display: flex;
            align-items: center;
            h1{
                font-size: 32px;
                font-weight: 700;
            }
            button{
                margin-left: 24px;
            }
        }
        &_search{
            display: flex;
            align-items: center;
            &_input{
                display: flex;
                align-items: center;
                justify-content: center;
                padding:16px 24px;
                border-radius: 30px;
                background:rgba(202,203,223, 0.5);
                input{
                    background: transparent;
                    margin-left: 12px;
                    color:rgba(23,28,59, 0.6);
                    caret-color: rgba(23,28,59, 0.6);
                    &::placeholder{
                        color:rgba(23,28,59, 0.6);
                    }
                }
            }
            &_filter{
                background:rgba(202,203,223, 0.5);
                padding:8px;
                margin-left: 8px;
            }
        }
    }
    &_projets{
        display: flex;
        flex-wrap: wrap;
        align-content:flex-start;
        overflow-y: scroll;
        height:calc(100% - 48px);
        margin-top: 48px;
        padding-bottom:48px;
        .projet{
            width:calc(100%/3 - 16px);
            border: 1px solid #E5E5EF;
            border-radius: 16px;
            margin-right: 24px;
            margin-bottom: 24px;
            padding:16px 16px 24px 16px;
            &:nth-child(3n){
                margin-right: 0;
            }
            &_name_status{
                display: flex;
                align-items: center;
                justify-content: space-between;
                h4{
                    font-weight: 700;
                }
                span{
                    padding: 8px 10px;
                    border-radius: 30px;
                    background-color: #8C8EB5;
                    color:white;
                    font-size: 12px;
                    &.a-faire {
                        background-color: #8C8EB5;
                    }
                    &.termine {
                        background-color: #22C55E;
                    }
                    &.ferme {
                        background-color: #EF4444;
                    }
                    &.en-pause {
                        background-color: #FBBF24;
                    }
                    &.en-cours {
                        background-color: #3B82F6;
                    }
                }
            }
            &_teacher_tournage{
                display: flex;
                flex-direction: column;
                margin-top: 16px;
                span{
                    display: flex;
                    align-items: center;
                    margin-bottom: 8px;
                    img{
                        margin-right: 8px;
                    }
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}