.page_dashboard{
    padding:48px;
    height:calc(100vh - 75px);
    overflow: hidden;
    &_header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        &_title{
            display: flex;
            align-items: center;
            h1{
                font-size: 32px;
                font-weight: 700;
            }
            button{
                margin-left: 24px;
            }
        }
        &_search{
            display: flex;
            align-items: center;
            &_input{
                display: flex;
                align-items: center;
                justify-content: center;
                padding:16px 24px;
                border-radius: 30px;
                background:rgba(202,203,223, 0.5);
                input{
                    background: transparent;
                    margin-left: 12px;
                    color:rgba(23,28,59, 0.6);
                    caret-color: rgba(23,28,59, 0.6);
                    &::placeholder{
                        color:rgba(23,28,59, 0.6);
                    }
                }
            }
            &_filter{
                background:rgba(202,203,223, 0.5);
                padding:8px;
                margin-left: 8px;
            }
        }
    }
    &_projects_details{
        display: flex;
        flex-direction: column;
        overflow-y: scroll;
        height:calc(100% - 48px);
        margin-top: 48px;
        padding-bottom:48px;
        &_top{
            display: flex;
            align-items: flex-start;
            width: 100%;
            &_number{
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                width: 100%;
                padding:32px;
                border: 1px solid #E5E5EF;
                margin-right: 24px;
                border-radius: 16px;
                &_infos{
                    display: flex;
                    align-items: center;
                    &_image{
                        display: flex;
                        align-items: flex-start;
                        justify-content: center;
                        background:rgba(202,203,223, 0.5);
                        width: 50px;
                        height: 50px;
                        border-radius: 30px;
                        img{
                            height: 100%;
                            width: 100%;
                        }
                    }
                    &_text{
                        display: flex;
                        flex-direction: column;
                        margin-left: 16px;
                        span{
                            &:last-child{
                                font-weight: 700;
                            }
                        }
                    }
                }
                &_projet{
                    width:400px;
                    span{
                        color: #B43FA4;
                        font-weight: 500;
                        span{
                            &:first-child{
                                display: inline-block;
                                font-weight: 700;
                                font-size:40px;
                                margin-bottom: 8px;
                            }
                        }
                    }
                }
            }
            &_approuv_rej{
                width: 430px;
                &_rej,
                &_approuv{
                    display: flex;
                    align-items: center;
                    padding:10px 16px;
                    border: 1px solid #E5E5EF;
                    border-radius: 16px;
                    img{    
                        width: 40px;
                        height: 40px;
                        object-fit: cover;
                        margin-right: 16px;
                    }
                }
                &_approuv{
                    margin-bottom: 24px;
                }
            }
        }
        &_bottom{
            display: flex;
            width: 100%;
            height:calc(100% - 196px);
            margin-top: 24px;
            &_projets_en_cours{
                overflow-y: scroll;
                width: 100%;
                height: 100%;
                margin-right: 24px;
                padding:32px;
                border: 1px solid #E5E5EF;
                border-radius: 16px;
                h2{
                    margin-bottom: 32px;
                    font-size: 20px;
                    font-weight: 700;
                }
                .projet{
                    border: 1px solid #E5E5EF;
                    border-radius: 16px;
                    margin-bottom: 24px;
                    padding:16px 16px 24px 16px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                    &_name_status{
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        h4{
                            font-weight: 700;
                        }
                        span{
                            padding: 8px 10px;
                            border-radius: 30px;
                            background-color: #3B82F6;
                            color:white;
                            font-size: 12px;
                        }
                    }
                    &_teacher_tournage{
                        display: flex;
                        flex-direction: column;
                        margin-top: 16px;
                        span{
                            display: flex;
                            align-items: center;
                            margin-bottom: 8px;
                            img{
                                margin-right: 8px;
                            }
                            &:last-child{
                                margin-bottom: 0;
                            }
                        }
                    }
                }
            }
            &_calendar{
                width:413px;
                height:100%;
                max-height:383px;
                padding:32px;
                border: 1px solid #E5E5EF;
                border-radius: 16px;
                overflow-y: scroll;
                img{
                    width:100%;
                    object-fit: cover;
                }
            }
        }
    }
}