.page_login{
    position: relative;
    &_image{
        position: absolute;
        top:0;
        left:0;
        width:100%;
        height:100vh;
        object-fit: cover;
    }
    &_form{
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top:0;
        left:0;
        height: 100vh;
        width: 100%;
        background: rgba(38,48,105, 0.6);
        &_content{
            display: flex;
            flex-direction: column;
            align-items: center;
            padding:96px;
            background: #161C3D;
            width:600px;
            border-radius: 16px;
            img{
                width:250px;
                margin-bottom: 24px;
            }
            h2{
                font-size: 32px;
                color:white;
            }
            form{
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                input,
                button{
                    margin-top: 24px;
                }
                input{
                    padding:16px 24px;
                    border-radius: 30px;
                    width:100%;
                    background: #263069;
                    caret-color: white;
                    color:white;
                }
            }
        }
        
    }
}
